<template>
  <div class="list-page">
    <!-- header -->
    <v-toolbar dense color="primary" :dark="true">
      <v-icon class="mr-4" @click="backToHome">mdi-arrow-left</v-icon>

      <v-toolbar-title>
        <p class="text large">Random Picker</p>
        <p class="text small">Select / Edit List</p>
      </v-toolbar-title>

      <v-spacer></v-spacer>
    </v-toolbar>

    <!-- list of random picker packs -->
    <v-main>
      <center>
        <p class="text small grey--text text--darken-2 ma-4">
          {{ description }}
        </p>
      </center>

      <v-list flat dense>
        <v-list-item-group v-model="activeItem" color="pink">
          <template v-for="(item, index) in list">
            <v-list-item
              :key="'list-item_' + index"
              v-long-press="1000"
              @long-press-start="onLongPress(item)"
              @click="onClick($event, item)"
            >
              <!-- item name -->
              <v-list-item-content>
                <v-list-item-title class v-text="item.text"></v-list-item-title>
              </v-list-item-content>

              <v-spacer />

              <!-- delete icon -->
              <v-list-item-icon @click="showDeleteDialog($event, item)">
                <v-icon class="mx-2" color="grey" size="20px"
                  >mdi-delete-forever</v-icon
                >
              </v-list-item-icon>
            </v-list-item>

            <!-- items devider -->
            <v-divider v-if="index + 1 < list.length" :key="index"></v-divider>
          </template>
        </v-list-item-group>
      </v-list>

      <edit-name :dialog="addState" @save="updateList" />

      <delete-dialog
        :dialog="deleteState"
        text="Are you sure you want to delete the list:"
        @delete="deleteItem"
      />

      <v-fab-transition>
        <v-btn
          v-if="!addState.show"
          @click="showAddDialog()"
          fixed
          fab
          dark
          small
          bottom
          right
          color="pink"
        >
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-fab-transition>
    </v-main>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "List",

  components: {
    EditName: () => import("@/components/dialogs/EditName"),
    DeleteDialog: () => import("@/components/dialogs/DeleteDialog"),
  },

  data: () => ({
    activeItem: 0,

    deleteState: {
      show: false,
      item: null,
    },

    newItem: { text: "New List", id: null, options: [] },

    addState: {
      show: false,
      item: null,
    },

    clickState: {
      mouseDown: null,
      date: null,
    },
  }),

  methods: {
    onLongPress(item) {
      this.showOptions(item);
    },

    onClick(e, item) {
      this.changeActiveList(item);
    },

    changeActiveList(item) {
      const index = this.list.indexOf(item);
      if (index > -1) {
        this.activeItem = index.toString();
        this.$store.commit("changeActiveList", index);
        this.backToHome();
      }
    },

    showAddDialog(item = this.newItem) {
      this.addState.show = true;
      this.addState.item = item;
    },

    updateList() {
      this.addState.show = false;
      this.$store.commit("updateList", this.addState.item);
    },

    showDeleteDialog(event, item) {
      event.stopPropagation();
      this.deleteState.show = true;
      this.deleteState.item = item;
    },

    deleteItem() {
      this.deleteState.show = false;
      this.$store.commit("removeListItem", this.deleteState.item);
    },

    showOptions(item) {
      this.$router.push({ name: "Items", params: { id: item.id } });
    },

    backToHome() {
      this.$router.push({ name: "Home" });
    },
  },

  computed: {
    description: function() {
      if (this.list.length) {
        return "Press to select, Long Press to edit";
      }
      return "Press the + button to add new list.";
    },

    ...mapState(["activeList", "list"]),
  },

  created() {
    if (!this.activeList) {
      this.$store.commit("changeActiveList", 0);
    }

    this.activeItem =
      this.list.indexOf(this.activeList) > -1
        ? this.list.indexOf(this.activeList)
        : 0;
  },
};
</script>
